@use "./variables" as v;

html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    background-color: v.$violet;
    font-family: v.$body_font_family;
    font-weight: v.$body_font_weight;
    font-size: 16px;
    /*background-image: url(/images/bg-desktop.svg);*/
    background-image: url("/images/bg-desktop.svg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    line-height: 1.5;

    @include v.mobile {
        background-image: url(/images/bg-mobile.svg);
        background-repeat: no-repeat;
        background-attachment: fixed;
    }
}

h1 {
    font-family: v.$heading_font_family;
    font-weight: v.$heading_font_weight;
    @include v.mobile{
        text-align: center;
    }
}

h1,
p {
    color: v.$white;
}

p {
    @include v.mobile{
        text-align: center;
        margin: 0px 30px 30px 30px;
        word-spacing: 6.3px;
    }
}

button {
    border-radius: 50px;
    border: none;
    color: v.$violet;

    @include v.mobile {
        display: block;
        height: 45px;
        width: 206px;
        margin: auto;
        margin-bottom:30px;
    }
}

button:hover{
    background-color: v.$soft_magneta;
    color: v.$white;
}

.logo-image {
    height: 32px;
    display: block;
}

.illustration-image {
    max-width: 100%;
    @include v.mobile {
        margin-top: 68px;
        margin-bottom: 74px;
        margin-right: auto;
        margin-left: auto;
        display: block;
    }
}
.icons{
  @include v.mobile{
    display: flex;
    justify-content: center;
    align-items: center;
  }
   
}

i.fa {
    color: v.$white;
    display: inline-block;
    border-radius: 50px;
    box-shadow: 0 0 2px v.$white;
    padding: 0.5rem 0.6rem;
    margin-right: 10px;
}

i.fa:hover {
    color: v.$soft_magneta;
    display: inline-block;
    border-radius: 50px;
    box-shadow: 0 0 2px v.$soft_magneta;
    padding: 0.5em 0.6em;
    margin-right: 10px;
}

